.skill-section {
  width: 75%;
  min-height: 70vh;
  /* background-color: rgb(53, 52, 52); */

  background-color: #000;

  margin: 10rem auto;
  padding: 5rem 0;
  border-radius: 70px;
}
.skill {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3rem;
}

.introduction {
  width: 60%;
  max-width: 50rem;
  font-size: 1.1rem;
  letter-spacing: 1.3px;
  line-height: 1.5;
}

.effect {
  width: 60%;
  max-width: 50rem;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  position: relative;
  margin: 3rem 0;
}

.loop-wrap {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-shrink: 0;
  position: relative;
  overflow: hidden;
}

.loop {
  display: flex;
  width: fit-content;

  animation-name: loop;
  -webkit-animation-name: loop;
  animation-timing-function: linear;
  -webkit-animation-timing-function: linear;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
  animation-direction: var(--direction);
  -webkit-animation-direction: var(--direction);
  animation-duration: var(--duration);
  -webkit-animation-duration: var(--duration);
  margin-bottom: 10px;
  width: 100%;
}

.tag {
  display: flex;
  align-items: center;
  gap: 0 0.2rem;
  color: var(--clr-primary-3);
  font-size: 0.9rem;
  background-color: #334155;
  border-radius: 0.4rem;
  padding: 0.7rem 1rem;
  margin-right: 1rem;
  box-shadow: 0 0.1rem 0.2rem rgba(59, 58, 58, 0.2),
    0 0.1rem 0.5rem rgba(73, 71, 71, 0.3), 0 0.2rem 1.5rem rgb(0 0 0 / 40%);
  -webkit-box-shadow: 0 0.1rem 0.2rem rgba(59, 58, 58, 0.2),
    0 0.1rem 0.5rem rgba(73, 71, 71, 0.3), 0 0.2rem 1.5rem rgb(0 0 0 / 40%);
  height: 40px;
}

.tag span {
  font-size: 1.2rem;
  color: var(--clr-primary-3);
}

.fade {
  pointer-events: none;
  background: linear-gradient(
    90deg,
    #000,
    transparent 40%,
    transparent 60%,
    #000
  );
  position: absolute;
  inset: 0;
}

@keyframes loop {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(-50%);
  }
}
@media screen and (max-width: 761px) {
  .skill-section {
    width: 90%;
    padding: 3rem 0;
  }
  .introduction {
    width: 80%;
    font-size: 0.9rem;
    letter-spacing: 1px;
  }
  .effect {
    margin: 1rem 0;
    width: 85%;
  }

  .tag {
    gap: 0 0.1rem;
    font-size: 0.8rem;
    padding: 0.5rem 0.9rem;
    margin-right: 0.8rem;
    height: 36px;
  }
}
